// 'use strict';

// // Uncomment the next lines to use your game instance in the browser
const Game = require('../modules/Game.class');
const game = new Game();
const rows = 4;
const columns = 4;

const startButton = document.querySelector('.button.start');

startButton.addEventListener('click', () => {
  if (game.getStatus() === 'idle') {
    game.start();
  } else {
    const messageLose2 = document.querySelector('.message-lose');
    const messageWin = document.querySelector('.message-win');

    if (messageLose2) {
      messageLose2.style.display = 'none';
    }

    if (messageWin) {
      messageWin.classList.add('hidden');
    }

    game.restart();
  }

  updateGameField();

  // –ü—Ä–∏—Ö–æ–≤—É—é –ø–æ—á–∞—Ç–∫–æ–≤–µ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
  const messageStart = document.querySelector('.message-start');

  if (messageStart) {
    messageStart.style.display = 'none';
  }

  // –ó–º—ñ–Ω—é—é —Ç–µ–∫—Å—Ç –∫–Ω–æ–ø–∫–∏ –Ω–∞ "Restart"
  startButton.textContent = 'Restart';

  startButton.style.backgroundColor = 'red';
  startButton.style.color = 'white';
  startButton.style.border = 'none';
  startButton.style.fontSize = '16px';
});

// –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è –≥—Ä–∏ —Ç–∞ –ø—ñ–¥–∫–ª—é—á–µ–Ω–Ω—è –¥–æ DOM
document.addEventListener('keyup', handleKeyPress);

// –û–Ω–æ–≤–ª–µ–Ω–Ω—è —ñ–≥—Ä–æ–≤–æ–≥–æ –ø–æ–ª—è –≤ DOM
function updateGameField() {
  const board = game.getState();
  const fieldRows = document.querySelectorAll('.field-row');

  if (!fieldRows || fieldRows.length === 0) {
    return;
  }

  for (let r = 0; r < 4; r++) {
    const cells = fieldRows[r].children; // –ö–ª—ñ—Ç–∏–Ω–∫–∏ –≤ —Ä—è–¥–∫—É

    for (let c = 0; c < 4; c++) {
      const cell = cells[c];
      const num = board[r][c];

      updateCell(cell, num);
    }
  }

  // –û–Ω–æ–≤–ª—é—î–º–æ —Ä–∞—Ö—É–Ω–æ–∫
  document.querySelector('.game-score').textContent = `${game.getScore()}`;
}

// —Å—Ç–∏–ª—ñ –æ–∫—Ä–µ–º–æ—ó –∫–ª—ñ—Ç–∏–Ω–∫–∏
function updateCell(cell, num) {
  cell.textContent = '';
  cell.className = 'field-cell';

  if (num > 0) {
    cell.textContent = num;

    if (num <= 1024) {
      cell.classList.add(`field-cell--${num}`);
    } else {
      cell.classList.add('field-cell--2048');
    }
  }
}

// –û–±—Ä–æ–±–∫–∞ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—è –∫–ª–∞–≤—ñ—à
function handleKeyPress(e) {
  if (!game || game.getStatus() !== 'playing') {
    return;
  }

  switch (e.code) {
    case 'ArrowLeft':
      game.moveLeft();
      break;
    case 'ArrowRight':
      game.moveRight();
      break;
    case 'ArrowUp':
      game.moveUp();
      break;
    case 'ArrowDown':
      game.moveDown();
      break;
    default:
      return;
  }

  // –û–Ω–æ–≤–ª—é—é –ø–æ–ª–µ –ø—ñ—Å–ª—è –∫–æ–∂–Ω–æ–≥–æ —Ö–æ–¥—É
  updateGameField();

  // –ü–µ—Ä–µ–≤—ñ—Ä—é —Å—Ç–∞—Ç—É—Å –≥—Ä–∏
  if (checkVictory(game.getState())) {
    game.setStatus('win');
    showWinMessage();
  } else if (checkGameOver(game.getState())) {
    game.setStatus('lose');
    showLoseMessage();
  }
}

function showWinMessage() {
  const messangeWim = document.querySelector('.message-win');

  if (messangeWim) {
    messangeWim.classList.remove('hidden');
  }
}

function showLoseMessage() {
  const messageLose = document.querySelector('.message-lose');

  if (messageLose) {
    messageLose.style.display = 'block'; 
    messageLose.classList.remove('hidden');
  }
}

// —Ñ—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ–≤—ñ—Ä–∫–∏ –ø–µ—Ä–µ–º–æ–≥–∏
function checkVictory(board) {
  // –ø–µ—Ä–µ–≤—ñ—Ä—è—é –∫–æ–∂–Ω—É –∫–ª—ñ—Ç–∏–Ω–∫—É –Ω–∞ –Ω–∞—è–≤–Ω—ñ—Å—Ç—å 2048
  for (let r = 0; r < rows; r++) {
    for (let c = 0; c < columns; c++) {
      if (board[r][c] === 2048) {
        return true;
      }
    }
  }

  return false;
}

// —Ñ—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ–≤—ñ—Ä–∫–∏ Game Over
function checkGameOver(board) {
  // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ –Ω–∞—è–≤–Ω—ñ—Å—Ç—å –ø–æ—Ä–æ–∂–Ω—ñ—Ö –∫–ª—ñ—Ç–∏–Ω–æ–∫
  for (let r = 0; r < rows; r++) {
    for (let c = 0; c < columns; c++) {
      if (board[r][c] === 0) {
        return false; // –Ñ –ø–æ—Ä–æ–∂–Ω—è –∫–ª—ñ—Ç–∏–Ω–∫–∞, –≥—Ä–∞ –ø—Ä–æ–¥–æ–≤–∂—É—î—Ç—å—Å—è
      }

      // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –º–æ–∂–ª–∏–≤–æ—Å—Ç—ñ –∑–ª–∏—Ç—Ç—è –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—ñ
      if (c < columns - 1 && board[r][c] === board[r][c + 1]) {
        return false;
      }

      // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –º–æ–∂–ª–∏–≤–æ—Å—Ç—ñ –∑–ª–∏—Ç—Ç—è –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª—ñ
      if (r < rows - 1 && board[r][c] === board[r + 1][c]) {
        return false;
      }
    }
  }

  return true; // –ì—Ä—É –∑–∞–∫—ñ–Ω—á–µ–Ω–æ (–Ω–µ–º–∞—î –º–æ–∂–ª–∏–≤–∏—Ö —Ö–æ–¥—ñ–≤)
}
